<template>
	<el-form ref="form" label-width="80px" style="margin:20px; width: 60%; min-width:550px;">
		<el-form-item label="姓名">
		    <el-input style="width:200px;" v-model="student.name"></el-input>
		</el-form-item>
		<el-form-item label="性别">
			<el-radio-group v-model="student.sexy">
				<el-radio class="radio" :label="1">男</el-radio>
				<el-radio class="radio" :label="0">女</el-radio>
			</el-radio-group>
		</el-form-item>
		<el-form-item label="年龄">
			<el-input-number :min="0" :max="200" v-model="student.age"></el-input-number>
		</el-form-item>
		<el-form-item label="学号">
			<el-input type="text" style="width:200px;" v-model="student.sid"></el-input>
		</el-form-item>
		<el-form-item label="选择专业">
		    <el-select placeholder="请选择专业" v-model="student.classroom" style="width:200px;">
		      <el-option label="信息管理" value="信息管理"></el-option>
		      <el-option label="网页设计" value="网页设计"></el-option>
		      <el-option label="软件设计" value="信息管理"></el-option>
		      <el-option label="网络工程" value="网页设计"></el-option>
		    </el-select>
		</el-form-item>
		<el-form-item label="手机号码">
			<el-input type="text" style="width:200px;" v-model="student.phone"></el-input>
		</el-form-item>
		<el-form-item label="注册日期">
		    <el-col>
		      <el-date-picker type="date" placeholder="选择日期" style="width: 200px;" v-model="student.regDate"></el-date-picker>
		    </el-col>
		</el-form-item>
		<el-form-item>
		    <el-button type="primary" @click="onSubmit" :loading="addLoading">立即创建</el-button>
		</el-form-item>
	</el-form>
</template>
<script>
	import {addUsers} from '../../api/api';
	export default {
		data() {
			return {
				student: {
					sid: '',
					name: '',
					sexy: -1,
					age: 0,
					classroom: '',
					phone: '',
					regDate: ''
				},
				addLoading: false
			}
		},
		methods: {
			onSubmit: function(){
				this.$confirm('确认提交吗？', '提示', {}).then(() => {
					this.addLoading = true;
					let data = Object.assign({}, this.student);
					data.regDate = !data.regDate ? '' : (data.regDate).Format('yyyy-MM-dd');
					let params = {type: '2', data: data };
					addUsers(params).then((res) => {
						this.addLoading = false;
						this.$message({
							message: '提交成功',
							type: 'success'
						});
					});
				});
			}
		}
	}
</script>


